"use client";

import { useEffect, useState } from "react";
import styles from "./quiz.module.css";

interface Option {
  id: number;
  questionId: number;
  text: string;
}

interface Question {
  id: number;
  quizId: string;
  title: string;
  type: string;
  options: Option[];
}

interface QuizData {
  uuid: string;
  title: string;
  createdAt: string;
  questions: Question[];
}

interface QuizPageProps {
  params: { id: string };
}

export default function Quiz({ params }: QuizPageProps) {
  const paramsPromise = useParams();
  const params = use(paramsPromise); // "desembrulha" a Promise
  const quizId = params.id;
  const [quiz, setQuiz] = useState<QuizData | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchQuiz() {
      try {
        const res = await fetch(`http://localhost:3000/quizzes/${id}`); // Exemplo
        if (!res.ok) throw new Error("Error fetching quiz");
        const data = await res.json();
        setQuiz(data);
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    }

    fetchQuiz();
  }, []);

  if (loading) return <p className={styles.container}>Loading quiz...</p>;
  if (!quiz) return <p className={styles.container}>Quiz not found.</p>;

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>{quiz.title}</h1>
      <ul className={styles.questionsList}>
        {quiz.questions.map((question) => (
          <li key={question.id} className={styles.questionItem}>
            <p className={styles.questionTitle}>{question.title}</p>
            {question.options.length > 0 && (
              <ul className={styles.optionsList}>
                {question.options.map((option) => (
                  <li key={option.id}>{option.text}</li>
                ))}
              </ul>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}
